<template lang="html">

  <section class="user-list">
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <span class="card-title" style="font-size: 22px"><b></b></span>
            <hr style="margin-left: auto; margin-right: auto; background-color: black;" />
            <!-- buttons for management filtering: 1 row -->
            <b-button variant="dark" class="btn-fw btn-rounded normal-button" style="opacity: 0.7;"> (2313)</b-button>&nbsp;&nbsp;&nbsp;
            <b-button variant="dark" class="btn-fw btn-rounded normal-button"> (235)</b-button>&nbsp;&nbsp;
            <b-button variant="dark" class="btn-fw btn-rounded normal-button"> (235)</b-button>
            <b-button variant="dark" class="btn-fw btn-rounded normal-button"> (2313)</b-button>&nbsp;&nbsp;&nbsp;
            <b-button variant="dark" class="btn-fw btn-rounded normal-button"> (235)</b-button>&nbsp;&nbsp;
            <b-button variant="dark" class="btn-fw btn-rounded normal-button"> (212)</b-button>

            <!-- search input and buttons: 1 row -->
            <div class="button-panel row">
                <div class="search-box">
                    <i class="fa fa-search search-icon"></i>
                    <b-form-input class="search-input" placeholder="/ID"></b-form-input>
                </div>
                <div class="row">
                    <span class="normal-label"></span>
                    <datepicker placeholder="" format="yyyy-MM-dd"></datepicker>
                </div>&nbsp;&nbsp;&nbsp;
                <div class="row">
                    <span class="normal-label"></span>
                    <datepicker class="my-datepicker" placeholder="" format="yyyy-MM-dd"></datepicker>
                </div>
                <div style="position: absolute; right: 30px">
                    <b-button variant="dark" class="btn-rounded"><span style="font-size: 16px"></span></b-button>&nbsp;
                    <b-button variant="secondary" class="btn-rounded"><span style="font-size: 16px"></span></b-button>
                </div>
            </div>

            <!-- manangement table -->
            <div class="table-responsive" style="padding-top: 10px;">
              <table id="management_table" class="table center-aligned-table" style="text-align: center">
                <thead>
                  <tr>
                    <th class="border-bottom-0" style="width: 3%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 20%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 20%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 10%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 10%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 10%;"><b></b></th>
                    <th class="border-bottom-0" style="width: 10%"><b></b></th>
                    <th class="border-bottom-0" style="width: 7%"><b></b></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="management in managements" :key="management.id">
                    <td><input type="checkbox" true-value="yes" false-value="no"></td>
                    <td>{{management.id}}</td>
                    <td>{{management.time}}</td>
                    <td>{{management.name}}</td>
                    <td>{{management.category}}</td>
                    <td><label>{{management.device}}</label></td>
                    <td><label :style="[management.status == '' ? {color: 'red'} : {color: 'green'}]">{{management.status}}</label></td>
                    <td><b-button variant="dark" class="btn-fw" @click="$router.push('../management-detail')"></b-button></td>
                  </tr>
                </tbody>
              </table>
            </div>
            <b-pagination align="center" class="col-lg-2 offset-5" v-model="currentPage" :total-rows="getManagements()" :per-page="perPage" aria-controls="management_table" style="margin-top: 20px"></b-pagination>
          </div>
        </div>
      </div>
    </div>
  </section>

</template>

<script lang="js">
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'management-detail',
  components: { Datepicker },
  data () {
    return {
      perPage: 5,
      currentPage: 1,
      managements: [
        {
          id: '45685229878',
          time: '2020-08-07',
          name: 'v',
          category: '',
          device: 'IOS',
          status: ''
        },
        {
          id: '45685239878',
          time: '2020-08-07',
          name: '',
          category: '',
          device: '',
          status: ''
        },
        {
          id: '45683239878',
          time: '2020-08-07',
          name: 'v',
          category: '',
          device: '',
          status: ''
        },
        {
          id: '45621229878',
          time: '2020-08-07',
          name: '',
          category: 'H5',
          device: 'IOS',
          status: ''
        },
        {
          id: '45685329878',
          time: '2020-08-07',
          name: 'v',
          category: '',
          device: '',
          status: ''
        },
        {
          id: '45683569878',
          time: '2020-08-07',
          name: '',
          category: '',
          device: 'IOS',
          status: ''
        },
        {
          id: '45686539878',
          time: '2020-08-07',
          name: 'v',
          category: '',
          device: '',
          status: ''
        }
      ]
    }
  },
  methods: {
    getManagements () {
      return this.managements.length
    }
  },
  mounted () {
    this.$store.commit('setUrl', 'management-detail')
  }
}
</script>

<style scoped lang="scss">
.search-box {
  align-items: center;
  position: relative;
}

.search-icon {
  opacity: 0.5;
  padding: 12px 10px 0 20px;
  height: 40px;
  position: absolute;
}

.search-input {
  font-size: 15px;
  width: 250px;
  height: 40px;
  background-color: #dee2e6;
  border-radius: 20px;
  padding-left: 50px;
  color: black;
  margin-left: 5px;
}

.button-panel {
  padding-top: 20px;
  position: relative;
}

.normal-label {
  padding: 10px 0 0 20px;
}

.normal-text {
  font-size: 15px;
  margin-top: 2px;
}

.normal-button {
  height: 40px;
}

.page-item button {
  border: none;
}
</style>



